/* Imports ================================= */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

body {
	line-height: 1;
}

ol,
ul {
	list-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

* {
	box-sizing: border-box;
	margin: 0;
}

.word {
	font-size: 32px;
}

.mind-map-container {
	position: relative;
	width: 100vw;
	height: 100vh;
	overflow: scroll;
}

.mind-map-container .make-a-word {
	position: fixed;
	right: 50%;
	margin-top: 5%;
	font-size: 2rem;
	transform: translateX(50%);
}

.mind-map-container .make-a-word.showing {
	display: block;
}

.mind-map-container .make-a-word.hidden {
	display: none;
}

.mind-map-container .mind-map {
	position: absolute;
	width: 10000px;
	height: 10000px;
}

.mind-map-container .mind-map .word {
	position: absolute;
}

.mind-map-container .mind-map .debug {
	position: absolute;
	width: 5px;
	height: 5px;
	border-radius: 5px;
	font-size: 14pt;
	background-color: #ccc;
	color: #ccc;
	text-indent: 15px;
	display: none;
}

.mind-map-container .mind-map.debug .debug {
	display: block;
}

.controls {
	position: fixed;
	top: 0;
	right: 0;
	display: none;
}

.controls .save-word {
	margin-top: 5%;
	font-size: 2rem;
	display: none;
	transform: translateX(50%);
}

.controls .save-word.showing {
	display: block;
}

.controls .save-word.hidden {
	display: none;
}

.controls .pseudo-input {
	position: fixed;
	right: 50%;
	top: 50%;
	transform: translateX(50%);
	font-size: 1.5rem;
}

.controls .location-location-location {
	display: none;
}

.controls.showing {
	display: block;
}

.controls.hidden {
	display: none;
}

.hidden {
	display: none;
}
